@font-face {
  font-family: "Poppins";
  src: url(/src/assets/fonts/Poppins-Regular.ttf) format("truetype");
  font-weight: normal;
}
@font-face {
  font-family: "Poppins";
  src: url(/src/assets/fonts/Poppins-Medium.ttf) format("truetype");
  font-weight: 500;
}
@font-face {
  font-family: "Poppins";
  src: url(/src/assets/fonts/Poppins-SemiBold.ttf) format("truetype");
  font-weight: 600;
}
@font-face {
  font-family: "Poppins";
  src: url(/src/assets/fonts/Poppins-Bold.ttf) format("truetype");
  font-weight: 700;
}
@font-face {
  font-family: "Poppins";
  src: url(/src/assets/fonts/Poppins-Black.ttf) format("truetype");
  font-weight: 900;
}
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif, serif;
  font-weight: 500;
}

ul,
ol,
dl {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  border: none;
}

.wrapper {
  max-width: 18.75rem;
  width: 100%;
  margin: 0 auto;
}

.header {
  background-color: #63b5ff;
  max-height: 6.25rem;
  position: relative;
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.6875rem 0;
}
.header__contact {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  max-height: 1.875rem;
  justify-content: space-evenly;
  padding: 0.1875rem;
}
.header__contact-image-container {
  display: flex;
  align-items: center;
}
.header__content-image {
  margin-right: 0.25rem;
}
.header__contact-content {
  font-size: 0.75rem;
}

.nav {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  height: calc(100vh - 100px);
  overflow: hidden;
  transition: background 0.5s ease;
}
.nav--active {
  background-color: rgba(0, 0, 0, 0.5);
}
.nav__logo {
  font-size: 2rem;
}
.nav__list {
  border-top: 1px solid #3e4756;
  padding-top: 3.125rem;
  position: relative;
  top: 0;
  right: -100%;
  background: #ffffff;
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: right 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
.nav__list--active {
  right: calc(80% - 100vw);
}
.nav__social-media {
  width: 55%;
  display: flex;
  margin: 0 auto;
  justify-content: space-evenly;
  margin-top: 45px;
  padding: 25px;
  border-top: 1px solid #000;
}
.nav__social-media-container {
  width: 24px;
}
.nav__item {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
}
.nav__item--btn {
  background-color: #63b5ff;
  width: 75%;
  margin: 1.875rem auto 0;
}
.nav__link {
  display: block;
  font-size: 1.5rem;
  padding: 0.625rem 0;
  color: #3e4756;
}
.nav__toggle-btn {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.hamRotate.active {
  transform: rotate(45deg);
}

.hamRotate180.active {
  transform: rotate(180deg);
}

.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #3e4756;
  stroke-width: 5.5;
  stroke-linecap: round;
}

.nav__toggle-btn--animation .top {
  stroke-dasharray: 40 139;
}

.nav__toggle-btn--animation .bottom {
  stroke-dasharray: 40 180;
}

.nav__toggle-btn--animation.active .top {
  stroke-dashoffset: -98px;
}

.nav__toggle-btn--animation.active .bottom {
  stroke-dashoffset: -138px;
}